export const handler = async () => {
    try {
      return {
        statusCode: 200,
        body: JSON.stringify({ message: 'Hello, this is your AWS Lambda function!' }),
      };
    } catch (error) {
      console.error('Error:', error);
      return {
        statusCode: 500,
        body: JSON.stringify({ message: 'Internal Server Error' }),
      };
    }
};